import { Component, OnInit, NgZone } from '@angular/core';

import { Router } from '@angular/router';
import { FormGroup, FormBuilder } from "@angular/forms";
import { ProductsCrudService } from '../service/product-crud.service';
import { ActionSheetController } from '@ionic/angular';
import { UserPhoto, PhotoService } from '../service/photo.service';


@Component({
  selector: 'app-create',
  templateUrl: './create.page.html',
  styleUrls: ['./create.page.scss'],
})
export class CreatePage implements OnInit {

  userForm: FormGroup;
  

  constructor(
    private router: Router,
    public formBuilder: FormBuilder,
    private zone: NgZone,
    private userCrudService: ProductsCrudService,
    public photoService: PhotoService, 
    public actionSheetController: ActionSheetController  
  ) {
    this.userForm = this.formBuilder.group({
      lot: [''],
      name: ['']
    })
  }

  async ngOnInit() {
    await this.photoService.loadSaved();
  }


  onSubmit() {
    if (!this.userForm.valid) {
      return false;
    } else {
      this.userCrudService.createProducts(this.userForm.value)
        .subscribe((response) => {
          this.zone.run(() => {
            this.userForm.reset();
            this.router.navigate(['/list']);
          })
        });
    }
  }
  public async showActionSheet(photo: UserPhoto, position: number) {
    const actionSheet = await this.actionSheetController.create({
      header: 'Photos',
      buttons: [{
        text: 'Delete',
        role: 'destructive',
        icon: 'trash',
        handler: () => {
          this.photoService.deletePicture(photo, position);
        }
      }, {
        text: 'Cancel',
        icon: 'close',
        role: 'cancel',
        handler: () => {
          // Nothing to do, action sheet is automatically closed
         }
      }]
    });
    await actionSheet.present();
  }

}

